
import "../CSS/index.css";
import React, { useState } from 'react';

const Index = () => {
  const whoami = ["미래의 프로그래머", "가천대학생", "애묘가", "영화광"];
  const describe = [
    "자바와 리엑트를 주로 사용하는 미래의 프로그래머입니다.",
    "가천대학교 컴퓨터공학과 20학번 학생입니다.",
    "한 마리 고양이의 책임감있는 주인입니다.",
    "여러 종류의 영화를 좋아하고 섭렵하는 영화광입니다.",
  ];
  const image = [
      '/image/index1.jpg',
      '/image/index2.jpg',
      '/image/index3.jpg',
      '/image/index4.jpg'
  ]

  const [currentword, nextword] = useState(0);

  const gotonext = () => {
    nextword((currentword) => (currentword + 1) % whoami.length);
  };

  return (
    <div>
      <h1 id="selfintro_header">서민재의 포트폴리오에 오신 것을 환영합니다!</h1>
      <br />
      저를 한 문장으로 표현하자면?<br />
      <div className='middle_text'>저는 {whoami[currentword]}입니다.</div>
      <div ><img src={image[currentword]} alt='이미지'className="index_image"></img></div>
      
      <br />
      <button id="next_button" onClick={gotonext}>다음 문장</button>
      <p className='middle_text'>{describe[currentword]}</p>
      저에 대해 더 자세히 알고싶다면 우측 버튼을 통해 진입해 주세요!
    </div>
  );
};

export default Index;