import { Routes, Route, Link } from "react-router-dom";
import Hackathon from "./Contest/Hackathon";
import Scpc from "./Contest/Scpc";
import "./CSS/Contest.css";

function Contest() {
  return (
    <div className="container_contest">
      <div className="header_contest">공모전입니다.</div>
      <Link to="Hackathon" className="contest1" style={{width:'100px' ,height:'120px'}}> <img src="/image/contest_3.jpg" alt="react" style={{width:'100px' ,height:'120px'}}/> </Link> 
      <Link to="Scpc" className="contest2" style={{width:'100px' ,height:'120px'}}> <img src="/image/contest_4.jpg" alt="react" style={{width:'100px' ,height:'120px'}}/> </Link> <br />
      <div className="main_career">
        <Routes>
          <Route path="/" Component={Hackathon} />
          <Route path="Hackathon" Component={Hackathon} />
          <Route path="Scpc" Component={Scpc} />
        </Routes>
      </div>
    </div>
  );
}
export default Contest;
