import { Routes, Route, Link } from "react-router-dom";
import { useState } from "react";

import Resume from "./Intro/Resume";
import SelfIntro from "./Intro/SelfIntro";
import Login from "./Login";

import "./CSS/introduction.css";
function Introduction() {
  const [id, SetId] = useState("");
  const [pwd, SetPwd] = useState("");
  const changeId = (e) => {
    SetId(e.target.value);
  };
  const changePwd = (e) => {
    SetPwd(e.target.value);
  };
  if(id ==="aaaa" && pwd=== "1111"){
  return (
    <div className="container_introduction">

      <Routes className="main1">
        <Route path="/" Component={Resume} />
        <Route path="Resume" Component={Resume} />
        <Route path="SelfIntro" Component={SelfIntro} />
      </Routes>
      <Link to="Resume" className="button1"> 이력서 </Link> <br />
      <Link to="SelfIntro" className="button2"> 자기소개서 </Link> <br />
 
     
    </div>
  );
    }
    
    else
    return(
      <Login id={id} pwd={pwd} changeId={changeId} changePwd={changePwd}/> 
    );
    
}

export default Introduction;
