import { Routes, Route, Link } from "react-router-dom";
import Certificate from "./Career/Certificate";
import Volunteer from "./Career/Volunteer";
import Study from "./Career/Study";
import "./CSS/Career.css";
import React from 'react';
const Career = () => {
  return(
    <div className="container_career">

      <Routes className="main_career">
        <Route path="/" Component={Certificate} />
        <Route path="Certificate" Component={Certificate} />
        <Route path="Volunteer" Component={Volunteer} />
        <Route path="Study" Component={Study} />
      </Routes>

      <Link to="Certificate" className="button1_career"> 자격증 </Link> <br />
      <Link to="Volunteer" className="button2_career"> 봉사활동 </Link> <br />
      <Link to="Study" className="button3_career"> 학업 </Link> <br />
    </div>
  )
};

export default Career;