import "./CSS/Project.css";
import { Routes, Route, Link } from "react-router-dom";
import Movie from "./Project/Movie";
import Pet from "./Project/Pet";
import Washer from "./Project/Washer";
import React from 'react';

const projects = () => {
  return (
    <div className="container_Project">
      <Routes className="main_Project">
        <Route path="/" Component={Movie} />
        <Route path="Movie" Component={Movie} />
        <Route path="Pet" Component={Pet} />
        <Route path="Washer" Component={Washer} />
      </Routes>
      <Link to="Movie" className="movie_button"> 영화 </Link> <br />
      <Link to="Pet" className="pet_button"> 반려동물 </Link> <br />
      <Link to="Washer" className="washer_button"> 세탁기 </Link> <br />
    </div>
  );
};
export default projects;
