import React, { useState } from 'react';
import Calendar from 'react-calendar';
import "./CSS/Calendar.css";
import moment from "moment";
function Calender() {
  const [value, onChange] = useState(new Date());
  const date=moment(value).format("YYYY MM DD") === "2023 11 22";
  const result = date ? <h1>{moment(value).format("YYYY년 MM월 DD일에는 계획이 있습니다.")}</h1> : <h1>{moment(value).format("YYYY년 MM월 DD일에는 아무 계획 없습니다.")}</h1>;
  return (
    <div>
      <br />
      <div style={{fontSize:'80px'}}>나의 일정</div>
      <div style={{width:'100%', height:'600px',overflow:'scroll'}}>
        <Calendar onChange={onChange} value={value} />
      </div>
      <div style={{fontSize:'30px'}}>
        
       {result}
        
        
      </div>
    </div>
  );
}
export default Calender;